import {Inject, Injectable} from '@angular/core';
import {HttpEvent, HttpHandler, HttpInterceptor, HttpRequest, HttpResponse} from '@angular/common/http';
import {Observable} from 'rxjs';
import {BASE_URL_TOKEN} from './config';
import {filter, map} from 'rxjs/operators';

@Injectable()
export class CustomInterceptorService implements HttpInterceptor {

  constructor( @Inject(BASE_URL_TOKEN) private  readonly baseUrl: string) { }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    const headers = req.headers
      .append( 'Content-type', 'application/json')
      .append( 'secret-key', '$2b$10$VQpEdum.mMHDoOie7W1JveyoHsTboWny5jf0to9R35r4HUiUVRqL6');
    const jsonReq = req.clone({
      headers,
      url: `${this.baseUrl}${req.url}`,
    });
    console.log(this.baseUrl);
    return next.handle(jsonReq).pipe(
      filter(this.isResponse),
      map(res => {
        return res.clone({body: res?.body?.data});
      })
    );
  }
  private isResponse<T>(event: HttpEvent<T>): event is HttpResponse<T> {
    if (event instanceof HttpResponse) {
      return true;
    }
    return false;
  }
}
