import {
  AfterViewInit,
  Component,
  ContentChild,
  EventEmitter,
  OnInit,
  Output,
  TemplateRef,
  ViewChild, ViewContainerRef
} from '@angular/core';
import {MatDrawer} from '@angular/material/sidenav';

@Component({
  selector: 'ithub-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css'],
})
export class SidebarComponent implements OnInit, AfterViewInit {

  @ViewChild('drawer', {static: true})
  public drawer!: MatDrawer;

  @Output()
  public setSidenav: EventEmitter<MatDrawer> = new EventEmitter<MatDrawer>(true);

  @ViewChild('myView', {read: ViewContainerRef , static: false})
  public myView!: ViewContainerRef;

  @ContentChild('myContent', {static: false})
  public myContent!: TemplateRef<any>;


  constructor() {
  }

  ngOnInit(): void {
     this.setSidenav.emit(this.drawer);
  }


  ngAfterViewInit(): void {
    console.log(this.myView);
   Promise.resolve().then( () => {
    this.myView.createEmbeddedView(this.myContent, {$implicit: 'Some title', desc: 'my some description' });
    });
  }

}
